import React, { Component } from "react";
import "./cards.css";
import "bootstrap/dist/css/bootstrap.min.css";

class Cards extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div>
        <div className="card">
          <div className="col-xs-12 design-card"></div>
        </div>
        <div className="card card-box">
          <div className="card-header">
            <p>{this.props.values[1]} MULTIPLIER</p>
          </div>
          <div className="card-body">
            <div className="col-xs-4">
              <div className="dot">{this.props.values[0]}</div>
            </div>
          </div>
          <div className="card-body">
            <div className="col-xs-4">
              <h1>&darr;</h1>
            </div>
          </div>
          <div className="card-body">
            <div className="col-xs-3">
              <div className="dot">{this.props.values[0]}</div>
            </div>
            <div className="col-xs-1 horizontal-arrow">
              <span>&rarr;</span>
            </div>
            <div className="col-xs-3">
              <div className="dot">{this.props.values[0]}</div>
            </div>
            <div className="col-xs-1 horizontal-arrow">
              <span>&rarr;</span>
            </div>
            <div className="col-xs-3">
              <div className="dot">{this.props.values[0]}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Cards;
