import React, { Component } from "react";
import "./content.css";
import "bootstrap/dist/css/bootstrap.min.css";
import CopyToClipboard from "react-copy-to-clipboard";
import Services from "../../Services/services";

class Content extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: "https://freelancer.cash/?id=" + this.props.content_data.id,
      copied: false,
    };
  }

  async componentDidMount() {
    this.setState({
      value: "https://freelancer.cash/?id=" + this.props.content_data.id,
    });
  }

  onCopy = () => {
    this.setState({ copied: true });
  };

  copyFunction = (data) => {
    return <button className="button-class">{data}</button>;
  };

  render() {
    const {
      id,
      withdraw,
      earning,
      total_referrals,
      user_address,
    } = this.props.content_data;
    return (
      <div className="content-div">
        <h2 style={{ color: "white" }}>EARNINGS</h2>
        <h3 style={{ color: "white" }}>YOUR ADDRESS - {user_address}</h3>
        <div className="row">
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12 col-12">
            <span>YOUR ID -</span>
            <span> {id}</span>
          </div>
          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 content-box">
            <div className="inner-div">
              <span>CAN WITHDRAW - </span>
              <span> $</span>
              <span>{withdraw}</span>
            </div>
          </div>
          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 content-box">
            <div className="inner-div">
              <span>YOUR TOTAL EARNINGS - </span>
              <span> $</span>
              <span>{earning}</span>
            </div>
          </div>
          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 content-box">
            <div className="inner-div">
              <span>TOTAL REFERRALS - </span>
              <span> {total_referrals}</span>
            </div>
          </div>
          <div className="col-xl-6 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 content-box">
            <div className="inner-div">
              <span>REFERRAL LINK - </span>
              <div className="copy-link">
                <span>{this.state.value}</span>
              </div>
              <div style={{ marginBottom: "2px" }}>
                <CopyToClipboard
                  className="btn btn-default"
                  text={this.state.value}
                  style={{ height: "3rem", width: "8rem" }}
                  onCopy={this.onCopy}
                >
                  {this.state.copied
                    ? this.copyFunction("copied")
                    : this.copyFunction("copy")}
                </CopyToClipboard>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Content;
