import React, { Component } from "react";
import TronWeb from "tronweb";
import constant from "../../props/const";

class Form extends Component {
  constructor(props) {
    super(props);
    this.state = {
      tronWeb: null,
      contract_methods: null,
      private_key: constant.PRIVATE_KEY,
      contract_address: constant.CONTRACT_ADDRESS,
      amount: null,
    };
  }

  async componentDidMount() {
    var tries = 0;
    // CONNECT TO TRONLINK
    var timer = setInterval(async () => {
      if (tries >= 10) {
        const HttpProvider = await TronWeb.providers.HttpProvider;
        const fullNode = await new HttpProvider(
          "https://api.trongrid.io"
        );
        var tronWeb = await new TronWeb(fullNode, fullNode, fullNode);
        this.setState({
          tronWeb: tronWeb,
        });

        if (this.state.tronWeb) {
          await this.callStartingFunction();
        } else {
          alert("Please connect with tron wallet");
        }
        clearInterval(timer);
      }

      var tron_link = await window.tronWeb;
      if (tron_link) {
        if (tron_link.ready === true) {
          this.setState({
            tronWeb: tron_link,
          });
          await this.callStartingFunction();
          clearInterval(timer);
        }
      } else {
        tries++;
      }
    }, 1000);
  }

  callStartingFunction = async () => {
    // SET CONTRACT ADDRESS
    var contract_data = await this.state.tronWeb
      .contract()
      .at(this.state.contract_address);
    this.setState({
      contract_methods: contract_data,
    });
  };

  callRegister = async (e) => {
    e.preventDefault();
    try {
      this.state.contract_methods
        .withdraw(this.props.content_data.id, this.state.amount)
        .send({
          feeLimit: 10000000,
          callValue: 10000,
          shouldPollResponse: true,
        }).then(result => {
            alert("Successfully transferred your amount");
        })
        .catch(async (error) => {
          alert("Error in withdrawal");
        });
    } catch (error) {
      alert(error);
    }
  };

  handleChange = async (event) => {
    this.setState({
      amount: event.target.value,
    });
  };

  render() {
    const { contract_methods } = this.state;
    return (
      <div
        style={{
          backgroundColor: "white",
          textAlign: "center",
          height: "15rem",
          marginTop: "4rem",
          padding: "1rem",
        }}
      >
        <h3 style={{ color: "black" }}>WITHDRAW YOUR EARNINGS</h3>
        <input
          type="text"
          placeholder="Fill Amount"
          style={{ fontSize: "2rem" }}
          onChange={this.handleChange.bind(this)}
        />
        <input
          type="image"
          src="submit.png"
          name="submit1"
          onClick={this.callRegister}
          alt=""
          style={{
            border: "none",
            padding: "0px",
            width: "140px",
          }}
        />
      </div>
    );
  }
}

export default Form;
