import React, { Component } from "react";
import Properties from "../../props/const";

class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    const link = "https://shasta.tronscan.org/#/contract/" + Properties.CONTRACT_ADDRESS + "/code"
    return (
      <>
        <footer
          style={{
            textAlign: "center",
            color: "white",
            marginTop: "5rem",
            padding: "3rem",
            fontSize: "1.5rem",
          }}
        >
          <a href={link}>Contract Address - {Properties.CONTRACT_ADDRESS} </a>
          <small> Copyright &copy;  Freelancer 2020 All rights are reserved.</small>{" "}
        </footer>
      </>
    );
  }
}

export default Footer;
