import React from "react";
import { Component } from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Link, Redirect } from "react-router-dom";
import { toast } from "react-toastify";
import TronWeb from "tronweb";
import "./App.scss";
import "bootstrap/dist/css/bootstrap.min.css";
import constant from "./props/const";

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loader: true,
      tronWeb: null,
      contract_methods: null,
      private_key: constant.PRIVATE_KEY,
      contract_address: constant.CONTRACT_ADDRESS,
      isLoggedIn: false,
      endTime: null,
      login_value: null,
      id: null,
      withdraw: null,
      earning: null,
      total_referrals: null,
      invested_amount: null,
      contentData: [],
      amount: null,
      display: "none",
      login_or_signup: null,
      user_address: null,
    };
  }

  async componentDidMount() {
    toast.configure();
    var tries = 0;
    // CONNECT TO TRONLINK
    var timer = setInterval(async () => {
      if (tries >= 10) {
        const HttpProvider = await TronWeb.providers.HttpProvider;
        const fullNode = await new HttpProvider("https://api.trongrid.io");
        var tronWeb = await new TronWeb(fullNode, fullNode, fullNode);
        this.setState({
          tronWeb: tronWeb,
        });

        if (this.state.tronWeb) {
          await this.callStartingFunction();
          await this.register();
        } else {
          alert("Please connect with tron wallet");
        }
        clearInterval(timer);
      }

      var tron_link = await window.tronWeb;
      if (tron_link) {
        if (tron_link.ready === true) {
          this.setState({
            tronWeb: tron_link,
          });
          await this.callStartingFunction();
          await this.register();
          clearInterval(timer);
        }
      } else {
        tries++;
      }
    }, 1000);
  }

  register = async () => {
    var start = new Date();
    var currentTime = Date.parse(start) / 1000;
    var end = new Date();
    var endTime = new Date(end.setHours(end.getHours() + 24));
    var endTimeStamp = Date.parse(endTime) / 1000;
    this.setState({
      endTime: endTimeStamp,
    });
  };

  callStartingFunction = async () => {
    // SET CONTRACT ADDRESS
    var contract_data = await this.state.tronWeb
      .contract()
      .at(this.state.contract_address);
    this.setState({
      contract_methods: contract_data,
    });
  };

  async getBalance(address) {
    var balance = await this.state.tronWeb.trx.getBalance(address);
    return balance;
  }

  // 1 trx = 1000000
  callRegister = async (e) => {
    e.preventDefault();
    try {
      var balance;
      var amount =
        (this.state.amount + ((this.state.amount * 10) % 100)) * 32937527;

      var link_data = window.location.search;
      var referral_id = link_data.split("=")[1];

      // GET ALL ACCOUNTS
      var accounts = this.state.tronWeb.defaultAddress.base58;
      console.log("Accounts :::::", accounts);
      balance = await this.getBalance(this.state.contract_address);

      var reciept = await this.state.tronWeb.trx.sendTransaction(
        "TDmqywJLKysUNCRqsrQqWTbcn9uo9d6b5U",
        amount,
        { from: accounts }
      );
      balance = await this.getBalance(this.state.contract_address);

      if (reciept.result == true) {
        try {
          var tronWeb = new TronWeb({
            fullHost: "https://api.trongrid.io",
            privateKey: this.state.private_key,
          });
          var contract_functions = await tronWeb
            .contract()
            .at(this.state.contract_address);

          // SET LOGIN ID
          var array = await this.state.contract_methods.getEntries().call();
          var Id = array.length + 1;
          alert(
            "Login Id will be -> " +
              Id +
              "\n And wait for 5 minutes then \n Please confirm by checking your address after login"
          );

          await contract_functions
            .register(
              accounts,
              referral_id ? referral_id : 0,
              amount,
              this.state.endTime
            )
            .send({
              feeLimit: 10000000,
              callValue: 10000,
              shouldPollResponse: true,
            });
        } catch (error) {
          alert("Error in registration !, Your deducted fees will be refunded");
          // SEND TRX FROM CONTRACT TO USERS ADDRESS
          var tradeobj = await tronWeb.transactionBuilder.sendTrx(
            accounts,
            amount,
            "TDmqywJLKysUNCRqsrQqWTbcn9uo9d6b5U"
          );
          const signedtxn = await tronWeb.trx.sign(
            tradeobj,
            this.state.private_key
          );
          await tronWeb.trx.sendRawTransaction(signedtxn);
        }
      }
    } catch (error) {
      alert(error);
    }
  };

  handleChange = async (event) => {
    this.setState({
      login_value: event.target.value,
    });
  };

  login = async (event) => {
    event.preventDefault();
    var tronWeb = new TronWeb({
      fullHost: "https://api.trongrid.io",
      privateKey: this.state.private_key,
    });
    try {
      var contract_functions = await tronWeb
        .contract()
        .at(this.state.contract_address);
      var login_data = await contract_functions
        .login(this.state.login_value)
        .call();

      this.setState({ id: login_data[0].toNumber() });
      this.setState({ user_address: login_data[1] });
      this.setState({ withdraw: login_data[4].toNumber() });
      this.setState({ earning: login_data[5].toNumber() });

      var total_referrals = await contract_functions
        .totalReferrals(login_data[1])
        .call();

      this.setState({ total_referrals: total_referrals.toNumber() });
      this.setState({ isLoggedIn: true });
    } catch (error) {
      alert("Something went wrong");
    }
  };

  setAmount = async (event) => {
    event.preventDefault();
    var input = parseInt(event.target.value);
    this.state.amount += input;
    this.setState({
      amount: this.state.amount,
    });
  };

  changeDisplayOfLogin = (hideOrshow, login) => {
    if (hideOrshow == true) {
      this.setState({
        display: "hidden",
      });
      this.setState({
        login_or_signup: "Signup",
      });
    } else {
      this.setState({
        display: "inherit",
      });
      this.setState({
        login_or_signup: login,
      });
    }
  };

  setAmount = async (event) => {
    event.preventDefault();
    var input = parseInt(event.target.value);
    this.state.amount += input;
    this.setState({
      amount: this.state.amount,
    });
  };

  render() {
    var {
      display,
      login_or_signup,
      id,
      withdraw,
      earning,
      total_referrals,
      user_address,
      contract_methods,
    } = this.state;

    if (this.state.isLoggedIn) {
      return (
        <Redirect
          to={{
            pathname: this.props.pathData,
            state: {
              id: id,
              withdraw: withdraw,
              earning: earning,
              total_referrals: total_referrals,
              user_address: user_address,
            },
          }}
        />
      );
    } else {
      return (
        <div>
          <div className="preloader">
            <div id="g-spinner" className="loading">
              <img src="assets/images/selected/logo.png" />
            </div>
          </div>

          {login_or_signup != null ? (
            <div className="ac-wrapper" style={{ visibility: display }}>
              <div id="popup">
                <center
                  style={{ backgroundColor: "white", padding: "20px 40px" }}
                >
                  <a
                    style={{
                      background: "transparent",
                      color: "black",
                      cursor: "pointer",
                    }}
                    onClick={() => this.changeDisplayOfLogin(true, "Signup")}
                  >
                    Close
                  </a>
                  <br />
                  <h3 style={{ textAlign: "center", color: "#e78e21" }}>
                    {login_or_signup}
                  </h3>
                  <hr />
                  <div className="container-fluid">
                    <div className="row">
                      <div className="col-sm-12" style={{ padding: "0px" }}>
                        <div className="contact-form mb-30">
                          <form
                            name="form2"
                            className="form2"
                            id="form2"
                            onsubmit="return validateform();"
                          >
                            <input
                              type="text"
                              placeholder="Id"
                              required="required"
                              value={this.state.amount}
                              placeholder="$"
                              onChange={this.handleChange.bind(this)}
                              style={{
                                width: "100%",
                                paddingLeft: "10px",
                                marginBottom: "5px",
                                border: "1px solid rgba(0,0,0,0.4)",
                                color: "black",
                              }}
                            />
                            <div className="gap" style={{ marginTop: "5px" }} />
                            <div className="gap" />
                            {login_or_signup == "Signup" ? (
                              <div>
                                {contract_methods != null ? (
                                  <input
                                    type="image"
                                    src="submit.png"
                                    name="submit1"
                                    onClick={this.callRegister}
                                    alt=""
                                    style={{
                                      border: "none",
                                      padding: "0px",
                                      width: "140px",
                                    }}
                                  />
                                ) : (
                                  <p style={{ fontSize: "2rem" }}>
                                    {" "}
                                    Please activate Tron Wallet{" "}
                                  </p>
                                )}
                              </div>
                            ) : (
                              <input
                                type="image"
                                src="submit.png"
                                name="submit1"
                                onClick={this.login}
                                alt=""
                                style={{
                                  border: "none",
                                  padding: "0px",
                                  width: "140px",
                                }}
                              />
                            )}
                          </form>
                          {login_or_signup == "Signup" ? (
                            <div>
                              <button
                                className="button button-block"
                                value="20"
                                onClick={this.setAmount}
                              >
                                $ 20
                              </button>
                              <button
                                className="button button-block"
                                value="100"
                                onClick={this.setAmount}
                              >
                                $ 100
                              </button>
                            </div>
                          ) : (
                            <div></div>
                          )}
                          <p className="ajax-response" />
                        </div>
                      </div>
                    </div>
                  </div>
                </center>
              </div>
            </div>
          ) : (
            <div></div>
          )}

          <header className="header_wrap fixed-top">
            <div className="container">
              <nav className="navbar navbar-expand-lg">
                <a
                  className="navbar-brand page-scroll animation"
                  href="index-2.html"
                  data-animation="fadeInLeft"
                  data-animation-delay="0.8s"
                >
                  <img class="logo_light" src="logo-wide.png" alt="logo" />
                </a>
                <button
                  className="navbar-toggler animation"
                  data-animation="fadeInRight"
                  data-animation-delay="1.2s"
                  type="button"
                  data-toggle="collapse"
                  data-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                >
                  {" "}
                  <span className="ion-android-menu" />{" "}
                </button>
                <div
                  className="collapse navbar-collapse justify-content-end"
                  id="navbarSupportedContent"
                >
                  <ul
                    className="navbar-nav nav_btn animation"
                    data-animation="fadeInRight"
                    data-animation-delay="1.6s"
                  >
                    <li>
                      <a
                        className="btn btn-default"
                        onClick={() =>
                          this.changeDisplayOfLogin(false, "Login")
                        }
                      >
                        Login
                      </a>
                    </li>
                    <li>
                      <a
                        className="btn btn-default"
                        onClick={() =>
                          this.changeDisplayOfLogin(false, "Signup")
                        }
                      >
                        Signup
                      </a>
                    </li>
                  </ul>
                </div>
              </nav>
            </div>
          </header>

          <section
            id="home"
            className="banner_section section_gradiant_dark small_pb"
          >
            <div id="banner_bg_effect" className="banner_effect"></div>
            <div className="container">
              <div className="row align-items-center">
                <div className="col-lg-1 col-md-1 col-sm-12 order-lg-first text_md_center" />
                <div className="col-lg-10 col-md-10 col-sm-12 col-xs-12 order-lg-first text_md_center">
                  <div className="banner_text" style={{ textAlign: "center" }}>
                    <br />
                    <br />
                    <p
                      className="animation"
                      data-animation="fadeInUp"
                      data-animation-delay="1.5s"
                    >
                      TRON IS ONE OF THE LARGEST BLOCKCHAIN-BASED OPERATING
                      SYSTEMS IN THE WORLD.
                    </p>
                    <h1
                      className="animation custom-gradients"
                      data-animation="fadeInUp"
                      data-animation-delay="1.6s"
                      style={{
                        fontSize: "35px",
                        background:
                          "-webkit-linear-gradient(90deg, #fff96d, #ff9500) !important",
                        WebkitBackgroundClip: "text !important",
                        WebkitTextFillColor: "transparent !important",
                      }}
                    >
                      TRON WAS DEVELOPED AS AN ANSWER TO ETHEREUM LACK OF
                      SCALABILITY
                    </h1>
                  </div>
                </div>
                <div className="col-lg-1 col-md-1 col-sm-12 order-lg-first text_md_center" />
                <div className="col-lg-2 col-md-2 col-sm-12 order-lg-first text_md_center" />
                <div className="col-lg-8 col-md-8 col-sm-12 order-first">
                  <div
                    className="banner_image_right res_md_mb_50 res_xs_mb_30 animation"
                    data-animation="fadeInRight"
                    style={{ textAlign: "center" }}
                  >
                    <br />
                    <br />
                    <img
                      src="slider1.png"
                      alt="banner_vector2"
                      style={{ height: "12rem", width: "20rem" }}
                    />
                  </div>
                  <div style={{ width: "20rem", margin: "0 auto" }}>
                    <ul
                      className="navbar-nav nav_btn animation"
                      data-animation="fadeInRight"
                      data-animation-delay="0.6s"
                      style={{ flexDirection: "row", justifyContent: "center" }}
                    >
                      <li className="bottom-btn">
                        <button
                          className="btn btn-default"
                          style={{ height: "3rem", width: "8rem" }}
                          onClick={() =>
                            this.changeDisplayOfLogin(false, "Login")
                          }
                        >
                          Login
                        </button>
                      </li>
                      <li className="bottom-btn">
                        <button
                          className="btn btn-default"
                          style={{ height: "3rem", width: "8rem" }}
                          onClick={() =>
                            this.changeDisplayOfLogin(false, "Signup")
                          }
                        >
                          Signup
                        </button>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      );
    }
  }
}

export default Login;
