import React, { Component } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Link } from "react-router-dom";
import Cards from "../cards/cards.jsx";
import Form from "../Form/form.jsx";
import Content from "../content/content";
import Footer from "../footer/footer";
import "./dashboard.css";

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      firstCard: "2%",
      secondCard: "25%",
      thirdCard: "10%",
      firstContent: "DAILY",
      secondContent: "DIRECT",
      thirdContent: "LEVEL",
    };
  }

  componentDidMount() {
    // SET JS FUNCTION
    this.setJsFunction();

    // SET VALUES TO CARDS
    // this.setState({ firstCard: "2%" });
    // this.setState({ secondCard: "25%" });
    // this.setState({ thirdCard: "10%" });
  }

  setJsFunction() {
    let mainNav = document.getElementById("js-menu");
    let navBarToggle = document.getElementById("js-navbar-toggle");

    navBarToggle.addEventListener("click", function () {
      mainNav.classList.toggle("active");
    });
  }

  render() {
    return (
      <div>
        <nav className="navbar">
          <div className="dashboard-image">
            <span>
              <img src="logo-wide.png" alt="" />
            </span>
          </div>
          <span className="navbar-toggle" id="js-navbar-toggle">
            <i className="fa fa-bars"></i>
          </span>
          <ul className="main-nav" id="js-menu">
            <li>
              <a href="#" className="nav-links">
                About Us
              </a>
            </li>
            <li>
              <Link className="nav-links" to="/">
                Logout
              </Link>
            </li>
          </ul>
        </nav>
        <div className="container">
          <div className="row">
            <div className="col-xl-4 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 dashboard-box">
              <Cards
                values={[this.state.firstCard, this.state.firstContent]}
              ></Cards>
            </div>
            <div className="col-xl-4 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 dashboard-box">
              <Cards
                values={[this.state.secondCard, this.state.secondContent]}
              ></Cards>
            </div>
            <div className="col-xl-4 col-lg-6 col-md-6 col-sm-12 col-xs-12 col-12 dashboard-box">
              <Cards
                values={[this.state.thirdCard, this.state.thirdContent]}
              ></Cards>
            </div>
          </div>
          <div className="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12 col-12">
            <Form content_data={this.props.location.state} />
          </div>
          <Content content_data={this.props.location.state}></Content>
          <Footer></Footer>
        </div>
      </div>
    );
  }
}

export default Dashboard;
